@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
 
}

a {
  color: black; 
  text-decoration: none; 
}
.modalBox {
    position: absolute;
    background:rgba(19,19,19,0.2);
    width: 100%; 
    height: 100vh; 
    z-index: 1;
}
.inactive {
    display: none;
}
#modal-form {
    border: 1px solid gray;
    box-shadow: 1px 1px 1px black;
    overflow: hidden; 
    position: absolute; 
    top: 20%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); 
    width: 380px; 
    background-color: white; 
    border-radius: 5px; 
}
#modal-header{
    width: 100%; 
    padding: 2vh 0vh; 
    font-size: 1.2rem; 
    font-weight: bold; 
    background-color: rgb(250, 250, 250); 
    border-bottom: 1px solid rgb(54, 52, 52, .4); 
}
#header-message {
    margin: 0 10%; 
}
.form-field {
    margin: 2vh 0vh; 
}
.form-field > input {
    border-radius: 5px; 
    border: 1px solid #ddd; 
    width: 80%;
    padding: 5px; 
    margin: 2.5% 10%;
}
.form-field > input:focus {
    border: 1px solid gray; 
    outline: none; 
}
.form-field > label {
    font-weight: 600;
    margin: 0% 10%; 
}
#modal-footer {
    width: 100%; 
    display: flex; 
    justify-content: flex-end; 
    padding: 2vh 0vh; 
    background-color: rgb(250, 250, 250); 
    border-top: 1px solid rgb(54, 52, 52, .4); 
}
footer > button {
    cursor: pointer; 
    margin: 0vh 1.5vh; 
    border-radius: 3px; 
    font-size: .8rem; 
    font-weight: bold;
    padding: 3% 4%; 
    
}
#modal-submit {
    background-color: #db4c3f;
    color: white; 
}
@media screen and (max-width: 380px) {
    #modal-form{
        width: 350px; 
    }

}
.panel-wrapper{
    padding-top: 5vh; 
}
.navSideBar-Container {
    width: 20%; 
    min-width: 210px;
    background-color: rgb(250, 250, 250);  
}
.navSideBar-Container-hidden{
    display: none; 
}
.navSideBar-wrapper{
    background-color: rgb(250, 250, 250);
}
#projectsButton, .right-arrow{
    border: none; 
    padding: 0px 20px; 
    background-color: transparent; 
}
#projectsButton {
    font-size: 1rem; 
    font-weight: 500;
}
.right-arrow {
    cursor: pointer;
    font-size: 1.75rem; 
    -webkit-transition: .25s ease; 
    transition: .25s ease; 
}
.right-arrow:focus {
    outline: none; 
}
#navSideBar-header:hover {
    background-color: rgb(240, 240, 240); 
}
#plus {
    position: relative; 
    right: -3.5vh;
    opacity: .5;
}
#plus:hover {
    cursor: pointer;
    opacity: 1;
}
.rotate {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); 
}
.project-list-active {
    display: block; 
}
.project-list {
    display: none; 
}
.project-item:hover {
    background-color: rgb(255, 255, 255); 
}

@media screen and (max-width: 720px) {
    .navSideBar-Container {
        position: absolute;
        height: 100vh;
    }

}


html{
  height: 100%; 
  margin: 0; 
  box-sizing: border-box; 
}
body {
  font-family: 'Open Sans', sans-serif; 
  height: 100%; 
  margin: 0; 
}
h3 {
  margin: 0;
  padding: 20px 0px; 
  font-size: 2rem; 
  font-weight: 400; 
  width: 100%; 
  text-align: center;
  background-color: #c04a40; 
  color: rgb(255, 255, 255, .8); 
}
form {
  margin-top: 5vh; 
}
.listContainer {
display: flex; 
flex-direction: column; 
align-items: center;

}
.listItems {
  width: 50%; 
  padding: 15px; 
  border-bottom: 1px solid #292828; 
  list-style: none; 
}
#inputLabel {
  font-size: 1.2rem; 
  margin: 2vh; 
}
.itemContainer {
  display: flex; 
  justify-content: space-between; 
}
.itemContainer, .listItems, .toggleButton {
  padding: 1vh; 
  margin: 2vh 1vh; 
}
.wrapper {
  height: 100vh; 
  display: flex; 
}
#hamburger {
  display: none; 
}

@media screen and (max-width: 550px) {
  form {
    display: flex; 
    flex-direction: column; 
    align-items: center; 
  }
  form > input, label, button {
   margin: 5px 0px; 
  }
  
}

@media screen and (max-width: 720px) {
  #hamburger {
    cursor: pointer;
    display: block;
    font-size: 2rem;
    padding: 20px;
    color: rgb(255, 255, 255, .8);
  }
  .app-header {
    display: flex;
    background-color: #c04a40;
    flex-direction: row;
    align-items: center;
  }
  .listContainer {
    width: 100%; 
  }
  .listContainer > form {
    text-align: center; 
  }
}

